#integrations {
    table.checks {
        i.fas {
            &.fa-check, &.fa-check-double {
                color: $color-turquoise;
            }

            &.fa-times {
                color: $color-alizarin
            }
        }
    }

    .img-title {
        margin-top: 25px;
        margin-bottom: 10px;
    }
}